import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [{
            path: '/',
            redirect: '/index'
        },
        {
            path: '/index',
            name: 'index',
            component: require('@/components/Modules/index/').default,
            children: [{
                path: '/home',
                name: 'home',
                component: require('@/components/Modules/home/home').default,
            }, ]
        },

    ]
})